@media screen and (max-width: 320px) {

  .coursecard {
    width: 300px;
    align-self: center;
    justify-self: center;
    display: block;
    box-sizing: border-box;
    height: 330px;
    /* border: solid var(--inactive-nav-icon-color) 1px; */
    border: solid var(--light-separator-color) 1px;
    background-color: #f8f8fc;
    border-radius: 10px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    font-family: 'Arial, sans-serif';
    padding: 0px;
    /* margin-bottom: 30px; */
  }
  
  .sm-card-image-dashboard {
    position: relative;
    width: 280px;
    height: 132px;
    margin-left: calc((100% - 280px)/2);
    margin-top: 10px;
    border-radius: 10px;
    overflow: hidden;
    /* border: solid orange 1px; */
    background-color: #dcdce6;
    /* margin: 0px; */
  }
  
  .sm-card-image-dashboard img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .sm-best-seller-badge {
    position: absolute;
    top: 8px;
    right: 8px;
    background-color: transparent;
    color: var(--active-nav-btn-color);
    padding: 3px 8px;
    font-family: 'Mulish Bold';
    font-size: 12px;
    border-radius: 5px;
  }
  
  .sm-card-content {
    /* padding: 15px; */
    height: 198px;
    width: 300px;
    /* border: solid blue 1px; */
    padding-left: 15px;
    /* margin-top: -10px; */
    font-family: 'Mulish Bold';
  }
  
  .sm-card-title {
    font-family: 'Mulish Bold';
    font-size: 24px;
    font-weight: 600;
    /* margin: 10px 0; */
    color: #333;
  }
  
  .sm-author-info {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    /* margin-top: -5px; */
  }
  
  .sm-author-avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: #ccc;
    margin-right: 8px;
  }
  
  .sm-author-name {
    font-size: 14px;
    color: #666;
  }
  
  .sm-rating-info {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #f59e0b;
    margin-bottom: 8px;
  }
  
  .sm-rating-star {
    margin-right: 5px;
  }
  
  .sm-reviews {
    color: #888;
  }
  
  .sm-price-info {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .sm-current-price {
    font-size: 20px;
    font-weight: bold;
    color: #10b981;
    margin-right: 8px;
  }
  
  .sm-original-price {
    font-size: 14px;
    color: #888;
    text-decoration: line-through;
  }

  .sm-price-cart-div {
    position: absolute;
    width: 300px;
    height: 50px;
    /* border: solid red 1px; */
    margin-left: -15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px 0px 10px;
  }
  
  .sm-add-to-cart-btn {
    width: 120px;
    height: 35px;
    /* padding: 10px; */
    background-color: var(--active-nav-btn-color);
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-family: 'Mulish Bold';
    cursor: pointer;
  }
  
  .sm-add-to-cart-btn:hover {
    background-color: #5a4ecc;
  }

}


